import React, { Component } from 'react'
import { Link } from "react-router-dom"
import { login } from "../_services/user.service"

export default class Login extends Component {

  constructor(props) {
    super(props)

    this.state = {
      email: '',
      password: '',
      error: null,
      isLoaded: false,
      result: null
    }

    this.handleSubmit = this.handleSubmit.bind(this)
    this.handleChange = this.handleChange.bind(this)
  }

  handleChange(e) {
    const { name, value } = e.target
    this.setState({ [name]: value })
  }

  handleSubmit(e) {
    e.preventDefault()

    const { email, password } = this.state
    this.setState({ isLoaded: true })

    login(email, password)
      .then(
        result => {
          if (!result.data) {
            this.setState({ isLoaded: false, result })
          }
        },
        error => {
          this.setState({ isLoaded: false, error })
        }
      )
  }

  render() {
    const { email, password, error, result, isLoaded } = this.state
    if (error) {
      return <div className="alert alert-danger" role="alert">Servidor error!</div>;
    } else {
      return (
        <div>
          <div className="jumbotron jumbotron-fluid bg-primary text-white">
            <div className="container">
              <h1 className="display-4">Login</h1>
            </div>
          </div>
          <div className="container">
            <form name="form" onSubmit={this.handleSubmit}>
              <div className="form-group">
                <label>Email address</label>
                <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" name="email" value={email} onChange={this.handleChange} />
              </div>
              <div className="form-group">
                <label>Password</label>
                <input type="password" className="form-control" id="exampleInputPassword1" name="password" value={password} onChange={this.handleChange} />
              </div>

              {result &&
                <div class="alert alert-danger" role="alert">{result.message}</div>
              }

              <button className="btn btn-primary">Login</button>
              {isLoaded &&
                <img src="data:image/gif;base64," alt=""/>

              }

              <Link to="/register">Register</Link>

            </form>
          </div>
        </div>
      )
    }
  }
}


