import React, { Component } from 'react'
import { register } from "../_services/user.service"

export default class Register extends Component {
  constructor(props) {
    super(props)

    this.state = {
      user: {
        name: '',
        email: '',
        password: '',
      },
      error: null,
      result: null,
      isLoaded: false
    }

    this.handleSubmit = this.handleSubmit.bind(this)
    this.handleChange = this.handleChange.bind(this)
  }

  handleChange(e) {
    const { name, value } = e.target
    const { user } = this.state
    this.setState({
      user: {
        ...user,
        [name]: value
      }
    })
  }

  handleSubmit(e) {
    e.preventDefault()

    this.setState({ isLoaded: true })

    const { user } = this.state
    if (user.name && user.email && user.password) {
      register(user)
        .then(
          result => {
            if (!result.data) {
              this.setState({ isLoaded: false, result })
              window.location.href = "/login"
            }
          },
          error => {
            this.setState({ isLoaded: false, error });
          }
        )
    }
  }

  render() {
    const { user, error, result, isLoaded } = this.state
    if (error) {
      return <div>Error: {error}</div>;
    } else {
      return (
        <div>
          <div className="jumbotron jumbotron-fluid bg-primary text-white">
            <div className="container">
              <h1 className="display-4">Register</h1>
            </div>
          </div>
          <div className="container">
            <form name="form" onSubmit={this.handleSubmit}>
              <div className="form-group">
                <label>Name</label>
                <input type="text" className="form-control" name="name" value={user.name} onChange={this.handleChange} />
              </div>
              <div className="form-group">
                <label>Email address</label>
                <input type="email" className="form-control" name="email" value={user.email} onChange={this.handleChange} />
              </div>
              <div className="form-group">
                <label>Password</label>
                <input type="password" className="form-control" name="password" value={user.password} onChange={this.handleChange} />
              </div>

              {result &&
                <div className="alert alert-primary" role="alert">{result.message}</div>
              }

              <button className="btn btn-primary">Register</button>
              {isLoaded &&
                <img src="data:image/gif;base64," alt=""/>
              }
            </form>
          </div>
        </div>
      )
    }
  }
}


